import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from '@services/modules/user/services/user.service';

@Component({
    selector: 'tz-mob-unauthorized-bookings',
    templateUrl: './mob-unauthorized-bookings.component.html',
    styleUrls: ['./mob-unauthorized-bookings.component.scss'],
})
export class MobUnauthorizedBookingsComponent implements OnInit {
    constructor(private location: Location, private userService: UserService, private router: Router) {}

    ngOnInit() {}

    public onGoToBack(): void {
        this.location.back();
    }

    public gotoMytrips(): void {
        this.userService.openLoginModel().then(
            () => {
                this.router.navigate(['bookings']);
            },
            () => {}
        );
    }
}
